import axios from 'axios'

export default {
  namespaced: true,
  state: {
    notifications: [],
    notificationsTotal: null
  },
  getters: {
    getNotifications: s => s.notifications,
    getNotificationsTotal: s => s.notificationsTotal,
    getNotificationsLength: s => s.notifications.length,
    getNotificationsTextType: s => type => {
      switch (type) {
        case 'POST':
          return '  '
        case 'POST_COMMENT':
          return ' '
        case 'COMMENT_COMMENT':
          return '   '
        case 'FRIEND_REQUEST':
          return '    '
        case 'FRIEND_BIRTHDAY':
          return '  !'
        case 'MESSAGE':
          return ' '
      }
    }
  },
  mutations: {
    setNotifications: (s, value) => s.notifications = value,
    setNotificationsTotal: (s, value) => s.notificationsTotal = value
  },
  actions: {
    async apiNotifications({ commit }) {
      await axios({
        url: 'notifications',
        method: 'GET'
      }).then(response => {
        //test
        // if (`${response.data.data.map(z => z.sent_time)}` !== `${state.notifications.map(z => z.sent_time)}`) {
        commit('setNotificationsTotal', response.data.total)
        commit('setNotifications', response.data.data)

        // }
        //    
        // setTimeout(() => {
        //   dispatch('apiNotifications')
        // }, 5000)
      }).catch(() => {})
    },
    async readNotifications({commit}) {
      await axios({
        url: 'notifications?all=true',
        method: 'PUT'
      }).then(response => {
        commit('setNotifications', response.data.data)
        commit('setNotificationsTotal', response.data.total)
      }).catch(() => {})
    },
    async readOneNotification({commit}, id) {
      await axios({
        url: 'notifications',
        method: 'PUT',
        params: {
          id: id,
          all: false
        }
      }).then(response => {
        commit('setNotifications', response.data.data)
        commit('setNotificationsTotal', response.data.total)
      }).catch(() => {})
    }
  }
}



