export default {
  namespaced: true,
  state: {
    status: 'success',
    text: '!',
    show: false,
    timeout: 3000
  },
  getters: {
    getState: s => s
  },
  mutations: {
    setInfo(state, value) {
      state.status = value.status
      state.text = value.text
    },
    toggleShow: s => s.show = !s.show
  },
  actions: {
    successAlert({
      commit,
      state
    }, value) {

      if (!this.show) {
        commit('setInfo', value)
        commit('toggleShow')
        setTimeout(() => {
          commit('toggleShow')
        }, state.timeout)
      }
      else {}
    },
    setAlert({
       commit,
       state
     }, value) {

      if (!this.show) commit('setInfo', value)

    },
    showAlert({ commit, state }) {

      if (!this.show) {
        commit('toggleShow')
        setTimeout(() => {
          commit('toggleShow')
        }, state.timeout)
      }
      else {}
    }
  }

}



