<script>
import { mapGetters, mapActions } from 'vuex'
import {connect, isStompActive, sendMessage} from "../utils/ws";

const INTERVAL_DIALOG_MS = 10000;
const INTERVAL_NOTIFICATIONS_MS = 10000;
const INTERVAL_UNREAD_COUNT_MS = 10000;
const INTERVAL_WS_RECONNECT_MS = 60000;

export default {
  mounted () {
    this.intervalForMessages = setInterval(() => {
      if (this.activeDialog && !isStompActive()) {
        this.loadFreshMessages(this.activeDialog.id).then(() => this.refreshDialogs()).then(() => this.getUnreadCountFromServer())
      }
    }, INTERVAL_DIALOG_MS)

    this.intervalForUnreadCount = setInterval(() => {
      if (!isStompActive() && !this.activeDialog) { this.getUnreadCountFromServer() }
    }, INTERVAL_UNREAD_COUNT_MS)

    this.intervalForNotifications = setInterval(() => {
      //      WS
      if (!isStompActive()) { this.apiNotifications() }
    }, INTERVAL_NOTIFICATIONS_MS)

    this.intervalForWSReconnect = setInterval(() => {
      if (!isStompActive() && localStorage.getItem('dev') !== 'yes') {
        connect()
        setTimeout(() => {
          sendMessage(localStorage.getItem('email'))
        }, 2000)
      }
    }, INTERVAL_WS_RECONNECT_MS)
  },
  computed: {
    ...mapGetters('profile/dialogs', ['activeDialog']),
  },
  methods: {
    ...mapActions('profile/dialogs', ['loadFreshMessages', 'refreshDialogs', 'getUnreadCountFromServer']),
    ...mapActions('profile/notifications', ['apiNotifications']),
  },
  beforeDestroy () {
    window.clearInterval(this.intervalForMessages)
    window.clearInterval(this.intervalForNotifications)
    window.clearInterval(this.intervalForUnreadCount)
    window.clearInterval(this.intervalForWSReconnect)
  },
  render: () => null,
}
</script>



