import SockJS from 'sockjs-client'
import { Stomp } from '@stomp/stompjs'
var stompClient = null
var handlers = null

export function connect() {
  stompClient = Stomp.over(function(){
            return new SockJS('http://localhost:8080/ws-connect')})
  stompClient.connect({},  frame => {
    console.log('Connected: ' + frame)
    stompClient.subscribe('/user/queue/updates', message => {
      handlers(message)
    })
  })
}

export function addHandler(handler){

  handlers = handler
}

export function disconnect() {
  if (stompClient.connected) {
    stompClient.disconnect()
    console.log("Disconnected")
  }
}

export function sendMessage(message) {
  stompClient.send("/app/register", {}, message)
}

export function isStompActive(){
  if (isStompClientExist())
  { return stompClient.connected }
  else { return false }
}

export function isStompClientExist(){
  return stompClient !== null
}



